.ak-roundtables-list {
  ul {
    margin:0;
    padding:0;
    list-style: none;
    li {
      margin:0 0 10px;
      padding:0;
      list-style: none;
    }
  }
  img.ak-roundtable-thumb {
    border-radius: 50%;
    width: 50px;
  }
  .ak-roundtable-name {
    display: inline-block;
    padding: 0 0 0 10px;
  }
  .ak-module-more-link {
    color: $gray-text;
  }
}
